//Normalize---------------------------------------------------------------------
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

//General-----------------------------------------------------------------------
body {
    background-color: rgb(10, 10, 53);
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: rgb(39, 39, 95);
}

header {
    background-color: whitesmoke;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    width: 70%;
    margin: 0 auto;
    padding: 2em;
}

//Links----------------------------------------------------------------------
a {
    text-decoration: none;
    color: rgb(25, 121, 150);
    margin-top: 2em;
}

a:hover {
    color: rgb(151, 16, 16);
}

//Structure------------------------------------------------------------------
.wrapper {
    background-color: whitesmoke;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 70%;
    margin: 0 auto;
    padding: 5em 0;
}


//Table----------------------------------------------------------------------
table {
    width: 50%;
    margin: 2em auto 6em auto;
}

th {
    text-align: left;
    font-size: 1.5rem;
    background-color: rgb(214, 235, 255);
}

td {
    padding: 0.5em 0;
}

//Form ----------------------------------------------------
form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    font-size: 2rem;
}

input, label {
    width: 50%;
}

label {
    margin: 1.5em 0 0.5em 0;
}

input {
    padding: 1em;
}

input[type=submit] {
    font-size: 2rem;
    color: whitesmoke;
    margin: 2em 0;
    background-color: rgb(25, 121, 150);
}

input[type=submit]:hover {
    background-color: rgb(214, 235, 255);
    color: rgb(39, 39, 95)
}

//Typography --------------------------------------------------------------
h1 {
    font-size: 4rem;
}


//Media queries ---------------------------------------------------------

//Max 2200px-----------------------------------
@media screen and (max-width: 2200px) {

    table {
        width: 70%;
    }

    input, label {
        width: 70%;
    }
}

//Max 1600px-----------------------------------
@media screen and (max-width: 1600px) {

    table {
        width: 90%;
    }

    input, label {
        width: 90%;
    }
}


//Max 1200px-----------------------------------
@media screen and (max-width: 1200px) {

    .wrapper {
        width: 90%;
    }

    header {
        width: 90%;
    }
}


//Max 950px-----------------------------------
@media screen and (max-width: 950px) {

    .wrapper {
        width: 99%;
    }

    header {
        width: 99%;
    }

    th {
        font-size: 1rem;
    }
}


//Max 600px-----------------------------------
@media screen and (max-width: 600px) {

    th {
        font-size: 0.8rem;
    }

    td {
        font-size: 0.8rem;
    }
}